* {
  margin: 0;
  padding: 0;
}
body {
  -webkit-user-select:none;
  -moz-user-select:none;
  -ms-user-select:none;
  user-select:none
}
header {
  height: 80px;
  background-color: navy;
  color: #fff;
  font-size: 2rem;
  line-height: 80px;
  font-weight: 900;
  padding-left: 1rem;
}
.box {
  border-bottom: 1px solid lightslategray;
  padding: 1rem;
  position: relative;
}
.good {
  position: absolute;
  right: 5%;
  top: 35%;
  font-weight: 900;
  cursor: pointer;
}
.good span {
  display: inline-block;
  width: 30px;
  text-align: center;
}
button {
  width: 100px;
  height: 40px;
  border: 0;
  margin-top: 20px;
  margin-left: 1rem;
  cursor: pointer;
}
