.modal{
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  text-align: center;
  background: #00000060;
  backdrop-filter: blur(2px);
  opacity: 0;
  pointer-events: none;
  transition: all .5s;
}
.wrapText {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 300px;
  height: auto;
  background: #fff;
  padding: 30px;
  border-radius: 30px;
}
.modal.active {
  opacity: 1;
  pointer-events: auto;
}
.modal h4 {
  margin-bottom: 1rem;
  font-size: 2rem;
  text-transform: capitalize;
}
.modal p:nth-of-type(1) {
  margin-bottom: 20px;
}
.modal p:nth-of-type(2) {
  font-size: 1.25rem;
  font-weight: 700;
}
.modal .dum {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 30px;
  border: none;
  border-radius: 20px;
  background: rgb(120, 0, 255);
  color: #fff;
  margin: 20px auto 0;
  cursor: pointer;
}